<template>
     <div class="delete">
            <div :class="['slider']" :style="styles">
                <div class="content" 
                    @touchstart='touchStart'
                    @touchmove='touchMove'
                    @touchend='touchEnd'
                    :style="deleteSlider"
                >
            <!--          -->   
                <slot></slot>
                </div>
                <div class="remove" ref='remove' v-on:click="increment">
                    
                </div>
        </div>
     </div>
</template>
<script>
    export default {
        props: [ 'stylesx', 'del'],
        data() {
            return {
                startX: 0, //
                endX: 0, //
                moveX: 0, //
                disX: 0, //
                deleteSlider: "", //,v-bind:style="deleteSlider"
                styles: {}
            };
        },
        watch:{
            stylesx(val, vals){
                if(typeof(val) == 'object'){
                    this.styles = val
                }
            },
            endX(val, vals){
                console.log(val)
            }
        },
        mounted(){
            this.styles = this.stylesx || {}
        },
        methods: {
            increment(){
                this.$emit('increment');
            },
            touchStart(ev) {
                ev = ev || event;
                //tounches1

                if (ev.touches.length == 1) {
                    // 
                    this.startX = ev.touches[0].clientX;
                }
            },
            touchMove(ev) {
                ev = ev || event;
                //
                let wd = this.$refs.remove.offsetWidth;
                if (ev.touches.length == 1) {
                    // 
                    this.moveX = ev.touches[0].clientX;

                    // 
                    this.disX = this.startX - this.moveX;
                    // 
                    if (this.disX < 0 || this.disX == 0) {
                        this.deleteSlider = "transform:translateX(0px)";
                        // 0
                    } else if (this.disX > 0) {
                        // *5
                        this.deleteSlider = "transform:translateX(-" + this.disX * 5 + "px)";

                        // 
                        if (this.disX * 5 >= wd) {
                            this.deleteSlider = "transform:translateX(-" + wd + "px)";
                        }
                    }
                }
            },
            touchEnd(ev) {
                ev = ev || event;
                let wd = this.$refs.remove.offsetWidth;
                if (ev.changedTouches.length == 1) {
                    let endX = ev.changedTouches[0].clientX;

                    this.disX = this.startX - endX;
                    //,

                    if (this.disX * 5 < wd / 2) {
                        this.deleteSlider = "transform:translateX(0px)";
                    } else {
                        // 
                        this.deleteSlider = "transform:translateX(-" + wd + "px)";
                    }
                }
            }
        }
    };
</script>
<style scoped lang="less" scoped>
    .slider {
        width: 100%;
        position: relative;
        user-select: none;
        .content {
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            background: #fff;
            z-index: 100;
            //    
            transition: 0.3s;
        }
        .remove {
            position: absolute;
            box-sizing: border-box;
            padding: 0 .25rem*2;
            height: 100%;
            background: #ff544c;
            right: 0;
            top: 0;
            color: #fff;
            text-align: center;
            font-size: 16px;
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }
</style>



